import React, { useState, useEffect } from 'react';
import './Payment.css';
import { useStateValue } from "./StateProvider";
import CheckoutProduct from "./CheckoutProduct";
import { Link, useHistory } from "react-router-dom";
import { CardElement, useStripe, useElements } from "@stripe/react-stripe-js";
import CurrencyFormat from "react-currency-format";
import { getBasketTotal } from "./reducer";
import axios from './axios';
import { db } from "./firebase";

import emailjs from 'emailjs-com';




function Payment() {
    const [{ basket, user }, dispatch] = useStateValue();
    const history = useHistory();

    const stripe = useStripe();
    const elements = useElements();

    const [deliveryAddress, setDeliveryAddress] = useState('');
    const [userName, setUserName] = useState('');
    const [userPhone, setUserPhone] = useState('');
    const [error, setError] = useState(null);
    
    useEffect(() => {
        // generate the special stripe secret which allows us to charge a customer



    }, [basket])

    const saveToDb = (event) => {
        event.preventDefault();
        var documentRef = db.collection("delivery-details").doc();
        documentRef.set({
            email: user.email,
            name: userName,
            phone: userPhone,
            address: deliveryAddress,
            totalAmount: getBasketTotal(basket),
            products:  basket
        });
        // console.log("suma");
        setDeliveryAddress('');

    }

    const confirmation= () =>(

        alert("Thankyou!  Your Order has been placed   ")

    ) 
    const confirmationEmail= () =>(alert("Email sent")

) 

    // Send Email
    function sendEmail(e) {
        e.preventDefault();
    
        emailjs.sendForm('gmail', 'template_ugnwwxa', e.target, 'user_RAtD7vSA6CxJIynv1BSZW')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
          e.target.reset();
      }






return (
    <div className='payment'>
        <div className='payment__container'>
            <h1>
                Checkout (
                        <Link to="/checkout">{basket?.length} items</Link>
                        )
                </h1>


            {/* Payment section - delivery address */}


<form onSubmit={sendEmail}>

        <div className='payment__section'>
                    <div className='payment__title'>
                            <h3>Email</h3>
                        </div>

                        <div className='payment__address'>
                            <p>{user?.email}</p>
                            </div>

                        <div className='payment__title'>
                            <h3>Name</h3>
                        </div>
                        <div className='payment__address'>
                            <input name='namee' value= {userName} onChange={(event) => setUserName(event.target.value)} cols="30" rows="10"></input>
                        </div>


                        <div className='payment__title'>
                            <h3>Phone</h3>
                        </div>
                        <div className='payment__address'>
                            <input name='ph' value= {userPhone} onChange={(event) => setUserPhone(event.target.value)} cols="30" rows="10"></input>
                        </div>




                            
                        
                    </div>

                    <div >
                            <h3 className="addres">Delivery Address</h3>
                        </div>
                        <div >
                            <textarea name="add" className="addres" value= {deliveryAddress} onChange={(event) => setDeliveryAddress(event.target.value)} cols="50" rows="10"></textarea>
                        </div>
                        <h3 className="addres">Click on the below button to recieve a confirmation mail from our textiles </h3>
                        <div className="addres">
                            <input type="email" name="emaill" className="email" value={user?.email}></input>
                        </div>
                        
                        <div className="addres">
                            <input onClick={confirmationEmail} type="submit" className="btn btn info" value="Send Email"></input>
                        </div>


</form>

    
            {/* Payment section - Review Items */}
            <div className='payment__section'>
                <div className='payment__title'>
                    <h3>Review items and delivery</h3>
                </div>
                <div className='payment__items'>
                    {basket.map(item => (
                        <CheckoutProduct
                            id={item.id}
                            title={item.title}
                            image={item.image}
                            price={item.price}
                            rating={item.rating}
                        />
                    ))}
                </div>
            </div>


            {/* Payment section - Payment method */}
            <div className='payment__section'>
                <div className="payment__title">
                    <h3>Confirm Payment  </h3>
                </div>
                <div className="payment__details">
                    {/* Stripe magic will go */}

                    <form>

                        <div className='payment__priceContainer'>
                            <CurrencyFormat
                                renderText={(value) => (
                                    <h3>Order Total: {value}</h3>
                                )}
                                decimalScale={2}
                                value={getBasketTotal(basket)}
                                displayType={"text"}
                                thousandSeparator={true}
                                prefix={"$"}
                            />
                            <button onClick={saveToDb}>
                                <span onClick={confirmation}>Buy Now</span>
                            </button>
                        </div>

                        {/* Errors */}
                        {error && <div>{error}</div>}
                    </form>
                </div>
            </div>
        </div>
    </div>
)
}

export default Payment;
